ul {
  font-family: 'acme', sans-serif;
}
.items {
  list-style: none;
  counter-reset: num;
}
.items:before {
  font-family: "lato-light", sans-serif;
  color: ;
  content: "check it out, don't be shy";
  display: block;
  margin-bottom: 1em;
  letter-spacing: 0.1em;
}
.items li {
  cursor: pointer;
  position: relative;
  padding-left: 0.8em;
  font-size: 1.2em;
  transition: -webkit-transform 0.8s 0.8s;
  -webkit-transition: -webkit-transform .8s 0.8s;
  transition: -webkit-transform .8s 0.8s;
  transition: transform .8s 0.8s;
  transition: transform .8s 0.8s, -webkit-transform .8s 0.8s;
  margin-bottom: 10px;
  font-family: "lato-light", sans-serif;
}
.items li:hover {
  /* -webkit-transform: scale(1.3);
          transform: scale(1.3); */
  /* margin: 0.3em 0;   */
  /* text-shadow: 0 0 1.5em pink; */
}
.items li.active:after {
  content: none;
}
.items li:before {
  position: absolute;
  left: 0.2em;
  /* counter-increment: num;
  content: '0' counter(num); */
  font-size: 0.5em;
}
.items li:after {
  content: '';
  display: block;
  border-bottom: 1px solid transparent;
  width: 0;
  -webkit-transition: width 17s;
  transition: width 17s;
}
.items li:hover:after {
  width: 100%;
  /* border-bottom: 1px dotted #fff; */
}
@-webkit-keyframes fade
  5%
    opacity: 0;
@keyframes fade
  5%
    opacity: 0;
100%
    opacity: 1;




